import React, { Dispatch, SetStateAction, useCallback, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { useWeb3React } from 'web3-react-core'
import { Button, Stack, IconButton } from '@mui/material'
import { styled } from '@mui/material/styles'
import useToast from 'hooks/useToast'
import { Button as ButtonStyled } from 'components/uikit/components/Button'
import { buttonUnstyledClasses } from '@mui/base/ButtonUnstyled'
import { ShareIcon } from 'components/uikit/components/Svg'
import { ContractTransaction } from 'ethers'
import { FlexWrap } from 'components/CommonComponent'
import { CollateralItem } from 'hooks/news/useCollateral'
import { usePositionAction } from 'hooks/news/usePoistion'
import { ToastTitle } from 'config/constants/contants'
import useSystemParams from 'hooks/useSystemParams'
import usePanel from 'hooks/usePanel'
import { ChainAddressUrl, ChainTxUrl } from 'config/constants/chains'
import { TransactionSubmittedInfo } from 'components/TransactionInfo'
import { SupportedMintTokens } from 'config/constants/mintToken'

const blue = {
  500: '#007FFF',
  600: '#0072E5',
  700: '#0059B2',
  color: '#2AA1FF',
}

const CustomButtonRoot = styled(Button)`
  //display: block;
  font-family: PingFangSC-Regular, sans-serif;
  text-transform: none;
  font-size: 0.875rem;
  border-radius: 8px;
  transition: all 150ms ease;
  cursor: pointer;
  border: none;
  padding: 0 !important;
  color: ${blue.color};
  text-align: left;
  display: block !important;
  &:hover {
    background-color: transparent;
  }

  &.${buttonUnstyledClasses.active} {
    background-color: transparent;
  }

  &.${buttonUnstyledClasses.focusVisible} {
    //box-shadow: 0 4px 20px 0 rgba(61, 71, 82, 0.1), 0 0 0 5px rgba(0, 127, 255, 0.5);
    outline: none;
  }

  &.${buttonUnstyledClasses.disabled} {
    opacity: 0.5;
    cursor: not-allowed;
  }
`

const AlertButton = styled(ButtonStyled)`
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.043) !important;
  border-radius: 14px !important;
  background: linear-gradient(270deg, #7ae9f1 0%, #61c3f7 100%) !important;
  font-size: 14px !important;
  height: 32px !important;
  line-height: 32px !important;
  color: #000 !important;
  font-weight: normal !important;
`

function Action({ row, refresh }: { row: CollateralItem; refresh: Dispatch<SetStateAction<boolean>> }) {
  const { setExpanded } = usePanel()
  const { account, active, library } = useWeb3React()
  const { isEnable, position, mintTokenInfo } = row
  const [openText, setOpenText] = useState<string>()
  const [txDatas, setTxInfo] = useState<ContractTransaction>()
  const { accountContract } = useSystemParams()
  const { toastInfo, toastError, toastSuccess } = useToast()
  const history = useHistory()

  const { handleChangeCurrentPosition } = usePositionAction()
  const { handleUpdatePosition } = usePositionAction()
  const onOpen = useCallback(async () => {
    if (active && account) {
      if (accountContract) {
        try {
          const txInfo = await accountContract.createPosition(mintTokenInfo.address)
          setOpenText(txInfo)
          if (txInfo !== undefined && txInfo.hash) {
            toastInfo(
              ToastTitle.submited,
              <TransactionSubmittedInfo content='Waiting for confirmation' txHash={txInfo.hash} />,
            )
            setTxInfo(txInfo)
            setOpenText('Enabling')
            const info = await txInfo.wait()

            if (info.confirmations !== 0) {
              const positionAddress = await accountContract.positions(account, mintTokenInfo.address)
              toastSuccess(
                ToastTitle.success,
                <>
                  <p style={{ marginBottom: 24 }}>
                    {mintTokenInfo.symbol} collateral account was enabled. You can go to short it now.
                  </p>
                  <FlexWrap style={{ flexDirection: 'row-reverse' }}>
                    <AlertButton
                      onClick={() => {
                        window.open(`${ChainAddressUrl}/${positionAddress}`)
                      }}
                    >
                      View on Explorer
                    </AlertButton>
                  </FlexWrap>
                </>,
              )
              await handleUpdatePosition(SupportedMintTokens[mintTokenInfo.symbol])
              setOpenText('')
            }
          }
        } catch (error: any) {
          if (error) {
            toastError(ToastTitle.failed, error?.reason)
          }
        }
      }
    }
  }, [
    active,
    account,
    accountContract,
    mintTokenInfo.address,
    mintTokenInfo.symbol,
    toastInfo,
    toastSuccess,
    handleUpdatePosition,
    toastError,
  ])
  const onWaiting = useCallback(async () => {
    if (txDatas) {
      const info = await library.getTransaction(txDatas.hash)
      if (info.confirmations === 0) {
        toastInfo(
          'Submitted',
          <>
            <p style={{ marginBottom: 24 }}>Waiting for confirmation</p>
            <FlexWrap style={{ flexDirection: 'row-reverse' }}>
              <AlertButton
                onClick={() => {
                  window.open(`${ChainTxUrl}/${txDatas.hash}`)
                }}
              >
                View on Explorer
              </AlertButton>
            </FlexWrap>
          </>,
        )
      } else {
        refresh((state) => !state)
      }
    }
  }, [library, refresh, toastInfo, txDatas])

  return (
    <div>
      <Stack direction="row" spacing={2}>
        {openText === 'Enabling' ? (
          <CustomButtonRoot onClick={onWaiting}>Enabling</CustomButtonRoot>
        ) : (
          <CustomButtonRoot onClick={onOpen} disabled={isEnable}>
            {!isEnable ? 'Enable' : 'Enabled'}
          </CustomButtonRoot>
        )}
        <CustomButtonRoot
          onClick={() => {
            handleChangeCurrentPosition(row.mintTokenInfo.symbol)
            setExpanded('panel1')
            history.push('/collateral/manage')
          }}
          disabled={!row.isEnable}
        >
          Manage
        </CustomButtonRoot>
        <IconButton
          onClick={() => {
            window.open(`${ChainAddressUrl}/${position}`)
          }}
          disabled={!row.isEnable}
        >
          <ShareIcon color={isEnable ? blue.color : '#ccc'} />
        </IconButton>
      </Stack>
    </div>
  )
}

export default Action
