import React, { useState } from 'react'
import { InputBase } from '@mui/material'
import SearchIcon from '@mui/icons-material/Search'
import { styled } from '@mui/material/styles'
import Card from 'components/Card'
import SwitchButton from 'components/Switch'
import { BG_COMMON_COLOR } from 'components/Menu/config'
import Table, { StyledTableCell } from 'components/Table'
import useCollateralList, { CollateralItem } from 'hooks/news/useCollateral'
import Logo from 'components/Logo'
import Title from './components/Title'
import Action from './components/Action'
import useHashPowerToken from '../../hooks/useHashPowerToken'

export const Search = styled('div')`
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  box-sizing: border-box;
  border-radius: 11px;
  height: 44px;
  margin-bottom: 34px;
  .opened {
    font-family: PingFangSC-regular, sans-serif;
    font-size: 12px;
    line-height: 17px;
  }
`
export const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}))
export const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  width: '100%',
  height: '44px',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    background: `${BG_COMMON_COLOR}`,
    boxSizing: 'border-box',
    borderRadius: '11px',
    border: '1px solid rgba(207, 216, 226, 0.5)',
    transition: theme.transitions.create('width'),
    width: '100%',
    height: '100%',
    caretColor: '#6ED6F3',
    '&::-webkit-search-cancel-button': {
      height: '1em',
      width: '1em',
      borderRadius: '50em',
      background: 'url(https://pro.fontawesome.com/releases/v5.10.0/svgs/solid/times-circle.svg) no-repeat 50% 50%',
      backgroundSize: 'contain',
      opacity: '0.3',
    },
  },
}))

const headCells = [
  {
    id: 'symbol',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Ticker',
  },
  /*  {
      id: 'liquidity',
      numeric: true,
      disablePadding: false,
      isSort: true,
      label: 'Liquidity',
    }, */
  {
    id: 'outstandingDebt',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Outstanding Debt',
  },
  {
    id: 'collateralRatio',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Collateral Ratio',
  },
  {
    id: 'prepaidInterestCoverage',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Prepaid Interest Coverage',
  },
  {
    id: 'action',
    numeric: false,
    disablePadding: false,
    isSort: false,
    label: 'Action',
  },
]

function Collateral() {
  const { setRefresh, refresh } = useHashPowerToken()
  const [holderText, setPlaceholder] = useState<string>('Ticker')
  const { collateralList, onSearch, searchVal, onSwitch } = useCollateralList()

  return (
    <div
      style={{
        width: '100%',
        height: '100%',
        paddingRight: 100,
      }}
    >
      <Title>Collateral</Title>
      <Card style={{ height: 'calc(100% - 200px)' }} key={refresh}>
        <Search>
          <SearchIconWrapper>
            <SearchIcon />
          </SearchIconWrapper>
          <StyledInputBase
            type="search"
            placeholder={holderText}
            inputProps={{ 'aria-label': 'search' }}
            value={searchVal}
            onInput={onSearch}
            onFocus={() => {
              setPlaceholder('')
            }}
            onBlur={() => {
              setPlaceholder('Ticker')
            }}
          />
          <SwitchButton onChange={onSwitch} /> <span className="opened">Enabled</span>
        </Search>

        <Table
          key={refresh}
          dataSource={collateralList}
          columns={headCells}
          pagination={false}
          content={(row: CollateralItem, index, labelId) => (
            <React.Fragment key={row.mintTokenInfo.address}>
              <StyledTableCell component="th" id={labelId} scope="row">
                <Logo token={row.mintTokenInfo.logo} symbol={row.mintTokenInfo.symbol} imgprops={
                  {
                    style: {
                      float: 'left',
                      display: 'block',
                      marginRight: '8px',
                    },
                  }
                } />
                {row.mintTokenInfo.symbol}
              </StyledTableCell>
              <StyledTableCell>
                {row.outstandingDebt}
              </StyledTableCell>
              <StyledTableCell>{row.displayRatio}</StyledTableCell>
              <StyledTableCell>{row.displayCoverage}</StyledTableCell>
              <StyledTableCell>
                <Action row={row} refresh={setRefresh} />
              </StyledTableCell>
            </React.Fragment>
          )}
        />
      </Card>
    </div>
  )
}

export default Collateral
