import { useCallback, useEffect, useState } from 'react'
import { isSupportedChainId } from 'utils/web3React'
import { useWeb3React } from 'web3-react-core'
import { MintToken, MintTokenKey, SupportedMintTokens } from 'config/constants/mintToken'
import { TokenUtils, toLargeUnits } from 'utils/transformHelper'
import { usePositionInfo } from './usePoistion'

export interface CollateralItem {
  collateralRatio: number
  prepaidInterestCoverage: number
  displayRatio: string
  displayCoverage: string
  outstandingDebt: number
  isEnable: boolean
  position: string
  mintTokenInfo: MintToken
  symbol: SupportedMintTokens
}

export default function useCollateralList() {
  const { chainId } = useWeb3React()
  const { positionAll } = usePositionInfo()
  const [searchVal, setSearchVal] = useState('')
  const [collateralList, setCollateralList] = useState<CollateralItem[]>()
  const [dataSource, setDataSource] = useState<CollateralItem[]>()
  useEffect(() => {
    if (!isSupportedChainId(chainId)) return
    const list: CollateralItem[] = []
    for (const key in positionAll) {
      if (Object.prototype.hasOwnProperty.call(positionAll, key)) {
        const element = positionAll[key as MintTokenKey]
        const { collateralRatio, positionAddress, prepaidInterestCoverage, mintTokenInfo, isEnable } = element
        list.push({
          collateralRatio,
          position: positionAddress,
          prepaidInterestCoverage,
          displayRatio:
            collateralRatio === Number.POSITIVE_INFINITY
              ? '+'
              : (collateralRatio && `${TokenUtils.formatTokenAmount(collateralRatio, 2)}%`) || `${collateralRatio}`,
          displayCoverage:
            collateralRatio === Number.POSITIVE_INFINITY ? '+' : `${Math.floor(prepaidInterestCoverage)} days`,
          outstandingDebt: Number(
            TokenUtils.formatFourDecimals(Number(toLargeUnits(element.balance.toNumber(), mintTokenInfo.decimals))),
          ),
          mintTokenInfo,
          isEnable,
          symbol: mintTokenInfo.symbol as unknown as SupportedMintTokens,
        })

        setCollateralList(list)
        setDataSource(list)
      }
    }
  }, [chainId, positionAll])

  const onSearch = useCallback((event) => {
    setSearchVal(event.target.value)
    setCollateralList((state) => {
      console.log('event.target.value', event.target.value, state)
      if (event.target.value) {
        return state.filter((it) =>
          it.mintTokenInfo.symbol.toLocaleLowerCase().includes(event.target.value.toLocaleLowerCase()),
        )
      }
      return dataSource
    })
  }, [dataSource])
  const onSwitch = useCallback(
    (event, val) => {
      if (val) {
        setCollateralList((state) => {
          if (searchVal) {
            return state.filter(
              (it) =>
                it.isEnable && it.mintTokenInfo.symbol.toLocaleLowerCase().includes(searchVal.toLocaleLowerCase()),
            )
          }
          return dataSource.filter((it) => it.isEnable)
        })
      } else {
        setCollateralList((state) => {
          if (searchVal) {
            return state.filter((it) =>
              it.mintTokenInfo.symbol.toLocaleLowerCase().includes(searchVal.toLocaleLowerCase()),
            )
          }
          return dataSource
        })
      }
    },
    [dataSource, searchVal],
  )

  return {
    collateralList,
    onSearch,
    searchVal,
    onSwitch,
  }
}
